import { NextRequest, NextResponse } from 'next/server'
import { supabaseAdmin } from '@/lib/supabase/server'
import type { WorkflowPerformance } from '@/lib/accelerator/types'

// ==========================================
// GET /api/v1/workflows/[workflowId]/analytics - Get workflow performance data
// ==========================================
export async function GET(
  request: NextRequest,
  { params }: { params: Promise<{ workflowId: string }> }
) {
  try {
    const { workflowId } = await params
    const supabase = supabaseAdmin
    const { searchParams } = new URL(request.url)

    const startDate = searchParams.get('startDate') || new Date(Date.now() - 30 * 24 * 60 * 60 * 1000).toISOString().split('T')[0]
    const endDate = searchParams.get('endDate') || new Date().toISOString().split('T')[0]

    // Get workflow details
    const { data: workflow, error: workflowError } = await supabase
      .from('workflows')
      .select('*')
      .eq('id', workflowId)
      .single()

    if (workflowError || !workflow) {
      return NextResponse.json(
        { success: false, error: 'Workflow not found' },
        { status: 404 }
      )
    }

    // Get daily analytics from workflow_analytics table
    const { data: dailyAnalytics } = await supabase
      .from('workflow_analytics')
      .select('*')
      .eq('workflow_id', workflowId)
      .gte('date', startDate)
      .lte('date', endDate)
      .order('date', { ascending: true })

    // Get execution stats
    const { data: executions } = await supabase
      .from('workflow_executions')
      .select('id, status, started_at, completed_at, contact_id')
      .eq('workflow_id', workflowId)
      .gte('triggered_at', `${startDate}T00:00:00`)
      .lte('triggered_at', `${endDate}T23:59:59`)

    // Calculate metrics
    const totalExecutions = executions?.length || 0
    const completedExecutions = executions?.filter(e => e.status === 'completed').length || 0
    const failedExecutions = executions?.filter(e => e.status === 'failed').length || 0
    const successRate = totalExecutions > 0 ? (completedExecutions / totalExecutions) * 100 : 0

    // Calculate average execution time
    const executionTimes = executions
      ?.filter(e => e.started_at && e.completed_at)
      .map(e => new Date(e.completed_at).getTime() - new Date(e.started_at).getTime()) || []
    const averageExecutionTime = executionTimes.length > 0
      ? executionTimes.reduce((a, b) => a + b, 0) / executionTimes.length / 1000
      : 0

    // Get unique contacts
    const uniqueContacts = new Set(executions?.filter(e => e.contact_id).map(e => e.contact_id))
    const contactsCompleted = new Set(
      executions?.filter(e => e.contact_id && e.status === 'completed').map(e => e.contact_id)
    )

    // Get action performance
    const { data: actionResults } = await supabase
      .from('action_execution_results')
      .select(`
        action_id,
        action_type,
        status,
        started_at,
        completed_at
      `)
      .in('workflow_execution_id', executions?.map(e => e.id) || [])

    // Aggregate action performance
    const actionPerformanceMap = new Map<string, {
      actionId: string
      actionType: string
      executionCount: number
      successCount: number
      totalTime: number
      count: number
    }>()

    actionResults?.forEach(result => {
      const key = result.action_id
      const existing = actionPerformanceMap.get(key) || {
        actionId: result.action_id,
        actionType: result.action_type,
        executionCount: 0,
        successCount: 0,
        totalTime: 0,
        count: 0
      }
      
      existing.executionCount++
      if (result.status === 'success') existing.successCount++
      if (result.started_at && result.completed_at) {
        existing.totalTime += new Date(result.completed_at).getTime() - new Date(result.started_at).getTime()
        existing.count++
      }
      
      actionPerformanceMap.set(key, existing)
    })

    const actionPerformance = Array.from(actionPerformanceMap.values()).map(ap => ({
      actionId: ap.actionId,
      actionType: ap.actionType,
      executionCount: ap.executionCount,
      successRate: ap.executionCount > 0 ? (ap.successCount / ap.executionCount) * 100 : 0,
      averageExecutionTime: ap.count > 0 ? ap.totalTime / ap.count / 1000 : 0
    }))

    // Get top errors
    const { data: errorLogs } = await supabase
      .from('workflow_logs')
      .select('message, action_type')
      .eq('workflow_id', workflowId)
      .eq('level', 'error')
      .gte('timestamp', `${startDate}T00:00:00`)
      .lte('timestamp', `${endDate}T23:59:59`)
      .limit(100)

    // Aggregate errors
    const errorCounts = new Map<string, { message: string; actionType: string; count: number }>()
    errorLogs?.forEach(log => {
      const key = `${log.message}:${log.action_type || 'unknown'}`
      const existing = errorCounts.get(key) || { message: log.message, actionType: log.action_type || 'unknown', count: 0 }
      existing.count++
      errorCounts.set(key, existing)
    })

    const topErrors = Array.from(errorCounts.values())
      .sort((a, b) => b.count - a.count)
      .slice(0, 10)
      .map(e => ({
        errorMessage: e.message,
        count: e.count,
        actionType: e.actionType
      }))

    // Build daily executions array
    const dailyExecutions: { date: string; executions: number; successRate: number }[] = []
    const dateMap = new Map<string, { total: number; completed: number }>()
    
    executions?.forEach(e => {
      const date = new Date(e.started_at || e.triggered_at || new Date()).toISOString().split('T')[0]
      const existing = dateMap.get(date) || { total: 0, completed: 0 }
      existing.total++
      if (e.status === 'completed') existing.completed++
      dateMap.set(date, existing)
    })

    // Fill in dates
    let currentDate = new Date(startDate)
    const end = new Date(endDate)
    while (currentDate <= end) {
      const dateStr = currentDate.toISOString().split('T')[0]
      const stats = dateMap.get(dateStr) || { total: 0, completed: 0 }
      dailyExecutions.push({
        date: dateStr,
        executions: stats.total,
        successRate: stats.total > 0 ? (stats.completed / stats.total) * 100 : 0
      })
      currentDate.setDate(currentDate.getDate() + 1)
    }

    const performance: WorkflowPerformance = {
      workflowId,
      workflowName: workflow.name,
      totalExecutions,
      successRate,
      averageExecutionTime,
      dailyExecutions,
      actionPerformance,
      topErrors,
      contactsEntered: uniqueContacts.size,
      contactsCompleted: contactsCompleted.size,
      dropOffRate: uniqueContacts.size > 0 
        ? ((uniqueContacts.size - contactsCompleted.size) / uniqueContacts.size) * 100 
        : 0,
      lastExecutedAt: workflow.last_executed_at,
      lastUpdated: new Date().toISOString()
    }

    return NextResponse.json({
      success: true,
      data: performance
    })

  } catch (error) {
    console.error('Error in GET /api/v1/workflows/[workflowId]/analytics:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}
